@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@200;300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.NavMain {
  background-color: #87a878;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  color: white;
  display: flex;
  align-items: center;
  padding: 20px;
  height: 60px;
  width: 100vw;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 99;
}

.NavMain .nav-list {
  margin-left: auto;
  display: flex;
}

.NavMain .nav-list li:not(:last-child) {
  margin-right: 20px;
}

.burger-icon {
  height: 5vh;
  width: auto;
  padding-right: 5vw;
}

nav {
  display: flex;
}

.hidden {
  display: none;
}

.lateral-menu {
  box-sizing: border-box;
  padding-left: 17px;
  padding-right: 17px;
  position: absolute;
  top: 0;
  left: 0;
  background-color: white;
  width: 70vw;
  height: 100vh;
  color: #87a878;
  font-size: 6vw;
  display: flex;
  flex-direction: column;
}

.cross img {
  padding-top: 10px;
  padding-bottom: 10px;
  height: 45px;
  width: auto;
}

.lateral-menu h4 {
  font-size: 1.4rem;
  padding-bottom: 10px;
  color: black;
}

.lateral-menu nav {
  display: flex;
  flex-direction: column;
}

.lateral-link {
  display: inline-block;
}

.lateral-menu hr {
  margin: 2px;
}

.opacity {
  position: absolute;
  top: 0;
  right: 0;
  width: 30vw;
  height: 100vh;
  background-color: black;
  opacity: 0.7
}

.login-logout {
  display: flex;
  flex-direction: row;
  position: absolute;
  bottom: 10px;
  font-size: 20px;
}

.logout,
.login {
  display: flex;
}

.login-logout img {
  height: 30px;
  width: auto;
  padding-right: 10px;
}

.allProducts {
  padding: 10px;
}

h1 {
  text-align: center;
  text-decoration: underline;
  margin-bottom: 20px;
}

table {
  margin: 10px 10px;
  padding: 0 10px;
  border-collapse: collapse;
}

tr:nth-child(even) {
  background-color: #87a878a2;
}

.add img {
  text-align: center;
  height: 4vh;
  width: auto;
}

.add {
  display: flex;
  justify-content: center;
}

.product-selected {
  display: flex;
  align-items: center;
}

.input-quantity input {
  margin-left: 7px;
  margin-right: 7px;
  text-align: center;
}

.User__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 20px;
}

.User__container label {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
}

.User__container input {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5px;
}

.LocationAutoComplete {
  position: relative;
}

.LocationAutoComplete-results {
  position: absolute;
  background-color: white;
  padding: 0;
  margin: 0;
  border: 1px solid #00000011;
  z-index: 10;
}

.LocationAutoComplete-items {
  list-style: none;
  border: 1px solid #00000011;
  cursor: pointer;
  padding: 10px 10px;
  background-color: white;
}

.LocationAutoComplete-items:hover {
  background-color: #0000ff11;
}

*,*::before,*::after{box-sizing:border-box}ul[class],ol[class]{padding:0}body,h1,h2,h3,h4,p,ul[class],ol[class],figure,blockquote,dl,dd{margin:0}html{scroll-behavior:smooth}body{min-height:100vh;text-rendering:optimizeSpeed;line-height:1.5}ul[class],ol[class]{list-style:none}a:not([class]){-webkit-text-decoration-skip:ink;text-decoration-skip-ink:auto}img{max-width:100%;display:block}article>*+*{margin-top:1em}input,button,textarea,select{font:inherit}img:not([alt]){-webkit-filter:blur(10px);filter:blur(10px)}@media(prefers-reduced-motion:reduce){*{animation-duration:.01ms !important;animation-iteration-count:1 !important;transition-duration:.01ms !important;scroll-behavior:auto !important}}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Manrope", sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

.link-underlined {
  text-decoration: underline;
}

ul {
  list-style-type: none;
}

.center-column {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.comments {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  padding: 2px 16px;
  margin: 5px 10px;
}

.producer {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  margin: 5px 10px;
  height: 300px;
  max-width: 250px;
  margin-bottom: 2rem;
  border: 0;
  flex-basis: 250px;
  flex-grow: 0;
  flex-shrink: 0;
}

.scrolling-wrapper {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  margin-top: 10px;
}

.scrolling-wrapper::-webkit-scrollbar {
  display: none;
}

footer {
  background-color: rgb(94, 94, 94);
  color: white;
  width: 100%;
}

